export default {
	data() {
		return {
			isSelect: false,
			addressList: []
		};
	},
	onShow() {
		this.getAddressList();
	},
	onLoad(e) {
		if (e.type == 'select') {
			this.isSelect = true;
		}
		this.getAddressList();
	},
	methods: {
		edit(row) {
			uni.navigateTo({
				url: "../edit/edit?type=edit&addressId=" + row.addressId
			})
		},
		add() {
			uni.navigateTo({
				url: "../edit/edit?type=add"
			})
		},
		select(row) {
			//是否需要返回地址(从订单确认页跳过来选收货地址)
			if (!this.isSelect) {
				return;
			}
			uni.setStorage({
				key: 'selectAddress',
				data: row,
				success() {
					uni.navigateBack();
				}
			})
		},
		getAddressList() {
			this.$api.getAddressList({}, res => {
				if (res.code) {
					this.addressList = res.data;
				}
			})
		}
	},
	filters: {
		StringOne(value) {
			if (!value) {
				return;
			}
			return value.charAt();
		}
	}
}
